.footer-container {
    flex-direction: column;
    background-color: #09142e;
    padding:3rem 2rem;

}
.footernav-container {
    flex-direction: row;
    justify-content: center;
    margin-left: 4rem;    
    margin-right: 4rem;    
    padding-bottom: 2rem;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}
.footernav-container > a {
    display:inline-block;
    color:white;
    font-size: 18px;
    line-height: 24px;
    font-weight: 500;
    padding:2rem 3rem;
}
.footernav-container > a > img {
    display: block;
    width:32px;
    height: 32px;
    margin: auto;
}
.footer-container.container, .container-fluid, .container-lg, .container-md, .container-sm, .container-xl, .container-xxl {
    margin-left: 0px;
    margin-right: 0px;
}
.footersvg-container {
    display: flex;
    padding-top: 3rem;
    flex-direction: column;
    align-items: center;
    color: rgba(255, 255, 255, 0.5);
}
