.wallet-container {
    padding:3rem;
    border: 1px solid rgb(229, 231, 235);
    border-radius: 24px;
}
.wallet-container > .row {
    display: flex;
    justify-content: center ;
    align-items: center;
    margin:2rem 3rem
}
select.select_net {
    padding:1rem;
    border: 1px solid rgb(229, 231, 235);
    border-radius: 0.75rem;
}
select.select_net:hover {
    box-shadow: none;
    outline: none;
}
select.select_net:focus {
    box-shadow: none;
    outline: none;
    border-color: rgb(229, 231, 235);
}

select.select_net > option {
    padding:0.5rem;
    border: 1px solid rgb(229, 231, 235);
    border-radius: 0.75rem;
}

.wallet-container .wallet-row {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding:2rem 2rem;
    border: 1px solid rgb(229, 231, 235);
    border-radius: 0.75rem;
}
input.form-check-input {
    width:20px;
    height:25px;
}
input.form-check-input.is-valid:focus, .was-validated .form-check-input:valid:focus {
    box-shadow: none;
}
input.form-check-input.is-valid, .was-validated .form-check-input:valid  {
    border-color: rgba(255,51,105);
}
input.form-check-input.is-valid:checked, .was-validated .form-check-input:valid:checked {
    background-color: rgba(255,51,105);
}

.wallet-container > div > button {    
    color:white;
    background-image: linear-gradient(180deg,#f69,#ff3369);
    border-radius: 0.75rem; 
    padding-top: 1rem;
    padding-bottom: 1rem;    
    font-weight: 700;
    font-size: 1.5rem;
    outline:none;
    box-shadow: none;
    border:none;

}
.wallet-container > div > button:focus {
    outline:none;
    box-shadow: none;
    border:none;    
}