.toggle-btn-container {
    display: flex;
    align-items: center;
    justify-content: center;        
}

.toggle-btn-group > button.active {
    color: rgba(255,51,105);
    border: 1.5px solid rgba(255,51,105);
    border-radius: 0.75rem;
}

.toggle-btn-group > button {
    
    padding-left: 4rem;
    padding-right: 4rem;
    padding-top: 0.7rem;
    padding-bottom: 0.7rem;
    font-weight: 700;

}
.btn-group.toggle-btn-group>.btn-group>.btn, .toggle-btn-container .btn-group>.btn:not(.dropdown-toggle).active{
    color: rgba(255,51,105);
    border: 1.5px solid rgba(255,51,105);
    border-radius: 0.75rem;
}
.toggle-btn-group > button:focus {
    outline:none;
    box-shadow: none;
}

.toggle-btn-group {
    border: 1px solid rgb(229, 231, 235);
    border-radius: 0.75rem;    
}
