.farming-container {
    padding-top: 6rem;    
}

.score-col {   
    margin-left: 1.5rem;
    text-align: left;
    color:  rgba(100,116,139);
}

.score-col .point-span {
    font-weight: 700;
}
.score-col .scores-span {
    padding-left: 1rem;
    font-size: 0.8rem;
}
.score-col .harvest_btn {
    font-weight:700;
    color:  rgba(100,116,139); 
    background-color: rgba(241,245,249);   
    margin-left: 3rem;
    border:none;
    outline: none;
}

.score-col .harvest_btn:focus, .score-col .harvest_btn:hover{
    outline:none;
    box-shadow: none;
    color:  rgba(100,116,139); 
    background-color: rgba(241,245,249);
}
.farm-toggle-col {
    display: flex;
    justify-content: flex-end;
}
.farm-toggle-group > button.active {
    color:black;
    background-image: linear-gradient(180deg,#e0e6ed,#cbd5e1);
    border-radius: 0.75rem;  
}
.farm-toggle-group > button {
    
    padding-left: 2.5rem;
    padding-right: 2.5rem;    
    font-weight: 700;

}

.farm-toggle-group .btn-group.toggle-btn-group>.btn-group:not(:last-child)>.btn, .farm-toggle-group .btn-group>.btn:not(:last-child):not(.dropdown-toggle).active{
    color: black;    
    border-radius: 0.75rem;
}

.farm-toggle-group > button:focus {
    outline:none;
    box-shadow: none;
}

.farm-toggle-group {
    border: 1px solid rgb(229, 231, 235);
    border-radius: 0.75rem;
}

.farm-toggle-group {
    margin-left: 1rem;
}
.farming-toggle-row.row {
    margin-bottom:3rem;
    width: 100%;
}
.harvest-container {
    text-align: left;
    border: 1px solid rgb(229, 231, 235);
    border-radius: 0.75rem;
    margin-top: 3rem;
    margin-bottom: 5em;
    padding:40px 16px;
}
.harvest-container .row {    
    margin-left: 20px;
    margin-right: 20px;
    padding-top: 20px;
    padding-bottom: 20px;
    
    
}
.harvest-row {
    border: 1px solid rgb(229, 231, 235);
    border-radius: 0.75rem;
    font-weight: 700;
}
.harvest-row {
    display: flex;
    align-items: center;    
}
.harvest-row > div {
    color: rgba(100,116,139);
}
.harvest-header {
    color: rgba(100,116,139);
    font-weight: 700;
}

.harvest-addcol {
    border: 1px solid rgb(229, 231, 235);
    color: rgba(100,116,139);
    border-radius: 0.75rem;
    font-weight: 700;
    padding: 1rem;
}


.harvest-addcol > div > button {    
    color:white;
    background-image: linear-gradient(180deg,#f69,#ff3369);
    border-radius: 0.75rem; 
    padding-left: 2.5rem;
    padding-right: 2.5rem;    
    font-weight: 700;
    outline:none;
    box-shadow: none;
    border:none;

}
.harvest-addcol > div > button:focus {
    outline:none;
    box-shadow: none;
    border:none;    
}
.nofilter-header {
    display: block;
    font-weight: 700;
    font-size: 2rem;
}
.stack-div {
    margin:2rem auto;
}

.harvest-row:hover {
    cursor: pointer;
}